import logo from './logo.svg';
import './App.css';
import { useState, useEffect } from 'react'
import { BrowserRouter as Router, Route} from 'react-router-dom'

import Header from './components/Header';
import Footer from './components/Footer';
import Tasks from './components/Tasks';
import AddTask from './components/AddTask';
import About from './components/About';

const App = () => {

  // let [showAddTask, setShowAddTask] = useState(false) - bad - should use a function
  let [showAddTask, setShowAddTask] = useState(() => { return false })

  // const [tasks, setTasks] = useState([]) - bad - should use a function
  const [tasks, setTasks] = useState(() => {
    return []
  })

useEffect(() => {
  // const fetchTasks = async () => {
  //   const result = await fetch('http://localhost:5000/Tasks')
  //   const data = await result.json()
  //   // setTasks(data)
  // }

  const getTasks = async () => {
    const tasksFromServer = await fetchTasks()
    setTasks(tasksFromServer)
  }

  getTasks()
}, []) //if you want to pass in any dependencies (we don't have any here)

const fetchTasks = async () => {
  const result = await fetch('https://grantjenkins.github.io/my-app/Tasks')
  const data = await result.json()
  return data
  // setTasks(data)
}

const fetchTask = async (id) => {
  const result = await fetch(`https://grantjenkins.github.io/my-app/Tasks/${id}`)
  const data = await result.json()
  return data
}

const showAddTasksForm = () => {
  setShowAddTask(showAddTask = !showAddTask)
}

// add task
const addTask = async (task) => {
  const result = await fetch('https://grantjenkins.github.io/my-app/Tasks', {
    method: 'POST',
    headers: {
      'Content-type': 'application/json'
    },
    body: JSON.stringify(task)
  })

  const data = await result.json()

  setTasks([...tasks, data])
}
// const addTask = (task) => {
//   const id = tasks.length + 1;
//   const newTask = { id, ...task }
//   setTasks([...tasks, newTask])
// }

// delete task
const deleteTask = async (id) => {
  await fetch(`https://grantjenkins.github.io/my-app/Tasks/${id}`, {
    method: 'DELETE'
  })

  setTasks(tasks.filter((task) => task.id !== id))
}

// toggle reminder
const toggleReminder = async (id) => {
  const taskToToggle = await fetchTask(id)
  const updatedTask = { ...taskToToggle, reminder: !taskToToggle.reminder }

  const result = await fetch(`https://grantjenkins.github.io/my-app/Tasks/${id}`, {
    method: 'PUT',
    headers: {
      'Content-type': 'application/json'
    },
    body: JSON.stringify(updatedTask)
  })

  const data = await result.json()

  setTasks(tasks.map((task) => task.id === id ? 
    { ...task, reminder: data.reminder } : task))
}

//showAddTask && ... is the same as ternary without the else

  return (
    <Router>
    <div className="container">
      <Header title="Task Tracker" toggleForm={showAddTasksForm} showAddTask={showAddTask} />
      <Route path='/' exact render={(props) => (
        <>
          {showAddTask && <AddTask onAdd={addTask}/>}
          {tasks.length > 0 ? <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder} /> : "No tasks to show"}
        </>
      )}/>
      <Route path='/about' component={About}/>
      <Footer/>
    </div>
    </Router>
  );
}

export default App;
