import PropTypes from 'prop-types'
import { useLocation } from 'react-router-dom'

import Button from './Button'

// const Header = (props) => {
    const Header = ({ title, toggleForm, showAddTask }) => { //destruct the props object

    const location = useLocation()

    return (
        <header className="header">
            <h1>{title}</h1>
            {location.pathname === '/' && <Button onClick={toggleForm} text={showAddTask ? 'Hide' : 'Show'} />}
            {/* <h2>{props.title}</h2> */}
        </header>
    )
}

Header.defaultProps = {
    title: "Task Tracker"
}

Header.propTypes = {
    title: PropTypes.string
}

export default Header
