import PropTypes from 'prop-types'

const Button = ({ text, onClick }) => {
    return (
        <button onClick={onClick} className="button">{text}</button>
    )
}

Button.defaultProps = {
    // click: () => {}
}

Button.propTypes = {
    text: PropTypes.string.isRequired,
    onClick: PropTypes.func.isRequired
}

export default Button
